
.diagonal-bg{
	position: fixed;
	top: 0;
	z-index: -2;
	width: 100%;
	height: 100%;

}

.misc-div {
 width: 100vw;
 height: 95vh;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;  
}

#top-albums {
 
}

#albums-main-description {
    width: 75%
}
#albums-main-content {
    width: 75%;
    background-image: linear-gradient(45deg, #fefbeb, #e6f0ee);
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 1px 1px rgba(0,0,0,0.25);
}

#albums-description {
    padding: 10px 20px;
    height: 30%
}

#albums-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-bottom: 30px;
    margin-top: 30px;
}


/* blur effect */
.album-image, .album-image-unfocus, .album-image-focus {
    width: 12%;
    margin: 5px 5px;
    padding: 1px;
    background-image: linear-gradient(45deg, #FCF4C5, #CCE0DC );
    border-radius: 1px;
}


.album-image-unfocus {
    filter: blur(2px);
    padding: 20px;
}

.album-image:hover, .album-image-unfocus:hover {
    filter: none;;
    cursor: pointer;
}

.album-image-focus {
    box-shadow: 2px 2px 2px rgba(0,0,0,0.5);

}

